<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`TermDeposit${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import TermDepositListLayout from '@/views/term-deposit/term-deposit-browse/TermDepositListLayout'
import TermDepositCardLayout from '@/views/term-deposit/term-deposit-browse/TermDepositCardLayout'
import TermDepositPivotLayout from '@/views/term-deposit/term-deposit-browse/TermDepositPivotLayout'
import TermDepositTableMixin from '@/mixins/term-deposit/TermDepositTableMixin'

export default {
  name: 'term-deposit-browse',
  mixins: [TermDepositTableMixin],
  components: {
    TermDepositListLayout,
    TermDepositCardLayout,
    TermDepositPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadDepositTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
