import TermDepositService from '@/services/term-deposit/TermDepositService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TermDepositTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  data: function () {
    return {
      depositTableData: [],
      depositTableContainer: 'loading-container',
      depositTableBusy: false,
      depositTableTotalRows: 0,
      depositTableCurrentPage: 1,
      depositTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('DEPOSIT_TITLE'),
        field: 'title'
      }, {
        headerName: this.$t('DEPOSIT_NUMBER'),
        field: 'accountNumber',
        format: 'Number'
      }, {
        headerName: this.$t('DEPOSIT_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('DEPOSIT_AMOUNT'),
        field: 'depositAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('START_DATE'),
        field: 'startDate',
        format: 'Date'
      }, {
        headerName: this.$t('MATURITY_DATE'),
        field: 'maturityDate',
        format: 'Date'
      }, {
        headerName: this.$t('INTEREST_RATE'),
        field: 'interestRate',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    depositTableNoDataText: function () {
      return this.depositTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    depositTableTotalPages: function () {
      return Math.ceil(this.depositTableTotalRows / this.depositTablePerPage)
    },
    depositTablePagingInfo: function () {
      return this.pagingInfo(this.depositTableCurrentPage, this.depositTablePerPage, this.depositTableTotalRows)
    }
  },
  methods: {
    loadDepositTableData (loadingContainerId) {
      let self = this
      this.depositTableBusy = true
      this.depositTableContainer = loadingContainerId || this.depositTableContainer
      this.startLoading(this.depositTableContainer)
      TermDepositService.termDeposits({}).then(function (response) {
        self.stopLoading(self.depositTableContainer)
        self.depositTableBusy = false
        self.depositTablePerPage = response.data.totalRows
        self.depositTableTotalRows = response.data.totalRows
        self.depositTableData = response.data.accountList || []
      }).catch(function (error) {
        self.stopLoading(self.depositTableContainer)
        self.depositTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshDepositTable () {
      this.loadDepositTableData()
    }
  },
  watch: {
    depositTableCurrentPage: function () {
      this.loadDepositTableData()
    }
  }
}

export default TermDepositTableMixin
