<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button v-if="parent.depositTableData.length > 0" class="p-3 mr-4" @click="exportXls" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
        <vs-button v-if="parent.depositTableData.length > 0" class="p-3 mr-4" @click="exportCsv" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="parent.refreshDepositTable" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <ag-grid-vue class="ag-theme-balham ag-pivot-layout"
                 :gridOptions="gridOptions"
                 :rowData="parent.depositTableData"
                 :sideBar="sideBar"
                 :statusBar="statusBar">
    </ag-grid-vue>
  </nv-card>
</template>

<script>
import { AgGridVue } from 'ag-grid-vue'
import PivotLayoutMixin from '@/mixins/commons/PivotLayoutMixin'

export default {
  name: 'term-deposit-pivot-layout',
  mixins: [PivotLayoutMixin],
  components: {
    AgGridVue
  },
  beforeMount () {
    this.gridOptions.columnDefs = [{
      headerName: this.$t('DEPOSIT_TITLE'),
      field: 'title'
    }, {
      headerName: this.$t('DEPOSIT_NUMBER'),
      field: 'accountNumber'
    }, {
      headerName: this.$t('DEPOSIT_TYPE'),
      field: 'typeName',
      enableRowGroup: true
    }, {
      headerName: this.$t('CURRENCY'),
      field: 'currencyAbbreviation',
      enableRowGroup: true
    }, {
      headerName: this.$t('DEPOSIT_AMOUNT'),
      field: 'depositAmount',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      valueFormatter: this.formatNumber,
      enableValue: true
    }, {
      headerName: this.$t('START_DATE'),
      field: 'startDate',
      filter: 'agDateColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatDate
    }, {
      headerName: this.$t('MATURITY_DATE'),
      field: 'maturityDate',
      filter: 'agDateColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatDate
    }, {
      headerName: this.$t('INTEREST_RATE'),
      field: 'interestRate',
      type: 'numericColumn',
      enableRowGroup: true,
      filter: 'agNumberColumnFilter',
      enableValue: true
    }]
  }
}
</script>
