import Endpoint from '@/services/Endpoint'

export default {
  termDeposits (payload) {
    return Endpoint().get('/s1/connect/termDeposit', {
      params: payload
    })
  },
  termDepositReceipt (payload) {
    return Endpoint().get(`/s1/connect/termDeposit/${payload.accountNumber}/receipt`, {
      params: payload
    })
  }
}
